<template>
  <div class="container">
    <div class="header">
      <div class="title">编辑公司</div>
      <div class="back" @click="hanleHide">
        <fa-icon icon-name="undo" />
        返回
      </div>
    </div>
    <el-divider />
    <div class="wrap">
      <el-row>
        <el-col :lg="16" :md="20" :sm="24" :xs="24">
          <el-form ref="editForm" v-loading="loading" :model="form" status-icon label-width="100px" @submit.native.prevent>
            <el-form-item label="公司名" prop="name">
              <el-input v-model="form.name" size="medium" placeholder="请填写公司名"/>
            </el-form-item>
            <el-form-item class="submit">
              <el-button type="primary" @click="submitForm('editForm')">保 存</el-button>
              <el-button @click="resetForm('editForm')">重 置</el-button>
            </el-form-item>
          </el-form>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import CompanyModel from '@/models/company'
export default {
  name: 'CompanyEdit',
  components: {},
  props: {
    editID: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      loading: false,
      form: {
        name: ''
      },
    }
  },
  computed: {},
  created() { },
  mounted() {
    this.getCompany()
  },
  methods: {
    async getCompany() {
      this.loading = true
      const res = await CompanyModel.getOne(this.editID)
      this.loading = false
      this.form.name = res.name
    },
    hanleHide() {
      this.$emit('hanleHide', false)
    },
    async submitForm() {
      const res = await CompanyModel.editOne(this.editID, this.form)
      this.$message.success('修改公司信息成功')
      this.hanleHide()
    },
    // 重置表单
    resetForm(formName) {
      this.getCompany()
    },
  },
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  .el-divider--horizontal {
    margin: 0;
  }
  .container {
    height: 100%
    .header {
      display flex
      justify-content space-between
      align-items center
      .title {
        height 59px
        line-height 59px
        font-size 16px
        font-weight 500
      }
      .back {
        margin-right 40px
        cursor pointer
      }
    }
    .wrap {
      padding: 20px;
    }
  }
</style>
