/**
 * 公司模块
 */

import {
  post,
  get,
  put,
  _delete
} from '@/utils/request'

export default class Company {
  /**
   *
   * @param {*} size 每页条(默认每页10条数据)
   * @param {*} page 第几页(默认获取第1页的数据；对于数据库而言是第0页)
   */
  static async getList(size, page) {
    const data = await get(`cms/company/list?page=${page}&size=${size}`)
    return data
  }

  // 企业入驻，入驻成功后，立马去建立一个属于该企业的最高级别的管理员
  static async createOne(name) {
    const data = await post('cms/company', name)
    return data
  }

  // 获取企业信息
  static async getOne(id) {
    const data = await get(`cms/company/${id}`)
    return data
  }

  // 修改企业信息
  static async editOne(id, name) {
    const data = await put(`cms/company/${id}`, name)
    return data
  }

  // 删除企业信息，则该企业下所有的用户信息都无法使用
  static async deleteOne(id) {
    const data = await _delete(`cms/company/${id}`)
    return data
  }
}
